/* Credits: http://webdesign.tutsplus.com/tutorials/creating-a-collection-of-css3-animated-pre-loaders--cms-21978 */
#preload_wrapper {
  position: absolute;
  padding: 100px;
  z-index: 100000;
  top: 0;
  left: 0;
  background-color: white;
  color: black;
  text-align: center;
  width: 100%;
  height: 100%;
  opacity: 1;
}
#preloader_3 {
  position: relative;
  width: 43px;
  top: 40%;
  margin: auto;
}
#preloader_3:before {
  width: 20px;
  height: 20px;
  border-radius: 20px;
  content: '';
  position: absolute;
  background: #8bc3ee;
  animation: preloader_3_before 1s infinite ease-in-out;
}
#preloader_3:after {
  width: 20px;
  height: 20px;
  border-radius: 20px;
  content: '';
  position: absolute;
  background: grey;
  left: 22px;
  animation: preloader_3_after 1s infinite ease-in-out;
}
@keyframes preloader_3_before {
  0% {
    transform: translateX(0px) rotate(0deg);
  }
  50% {
    transform: translateX(50px) scale(1.2) rotate(260deg);
    background: grey;
    border-radius: 0px;
  }
  100% {
    transform: translateX(0px) rotate(0deg);
  }
}
@keyframes preloader_3_after {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-50px) scale(1.2) rotate(-260deg);
    background: #8bc3ee;
    border-radius: 0px;
  }
  100% {
    transform: translateX(0px);
  }
}
/**
 * Fonts.
