}
/**
 * Top Banner.
 */
#banner {
  width: 100%;
  height: 100vh;
  text-align: center;
  color: white;
}
#banner .banner-wrapper {
  padding-top: 22vh;
  position: relative;
  z-index: 100;
}
#banner .big-text {
  display: block;
  font-weight: 600;
  font-size: 7.5em;
  letter-spacing: 0px;
  -webkit-font-smoothing: antialiased;
  font-family: qsiovwbzki;
  text-transform: uppercase;
}
#banner .small-text {
  display: block;
  font-size: 2.1em;
  text-transform: uppercase;
  margin-top: 1em;
}
#banner .button-wrapper {
  margin-top: 5.2em;
}
#banner .button-wrapper .button {
  color: white;
  border: 2px solid white;
  margin-right: 3em;
}
#banner .button-wrapper .button:hover {
  color: #ffe319;
  border: 2px solid #ffe319;
}
#banner .button-wrapper .button:last-child {
  margin-right: 0em;
}
.video-overlay {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  z-index: 15 ;
  background: -webkit-linear-gradient(top, rgba(5, 1, 5, 0.2), transparent);
  background: -o-linear-gradient(top, rgba(5, 1, 5, 0.2), transparent);
  background: -moz-linear-gradient(top, rgba(5, 1, 5, 0.2), transparent);
  background: linear-gradient(top, rgba(5, 1, 5, 0.2), transparent);
}
.video-background video {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  z-index: 5;
}
.trans-layer {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 50;
  background: rgba(255, 255, 255, 0.15);
}
