  font-size: 1.6em;
  z-index: 100;
  margin-top: 5px;
}
.modal-dialog .modal-body {
  padding: 10px 35px 35px 35px;
}
.modal-dialog h2 {
  margin: 0.5em auto 0.1em auto;
  line-height: 1;
}
.modal-dialog .undertitle {
  margin-bottom: 0.6em;
}
.modal-dialog .bs-callout {
  margin-top: -70px;
}
.modal-backdrop {
  background-color: rgba(176, 176, 176, 0.85);
}
/**
 * Blog.
 */
#blog .fbs-divider {
  margin-top: 0;
  padding-top: 55px;
}
#blog .blog-thumbnail-wrapper {
  overflow: hidden;
  display: block;
  width: 100%;
  position: relative;
}
#blog .blog-thumbnail-wrapper > img {
  display: block;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  position: relative;
  -webkit-transition: all 0.15s ease;
  -moz-transition: all 0.15s ease;
  -o-transition: all 0.15s ease;
  transition: all 0.15s ease;
}
#blog .thumbnail a.button {
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin-left: auto;
  margin-right: auto;
  z-index: 100;
}
#blog .blog-component-image {
  max-width: 100%;
}
#blog h6 {
  margin: 0px 0 3px 0;
}
